<template>
  <div id="courseware">
    <head-top title="课件"></head-top>

    <van-pull-refresh v-model="loading" @refresh="getDataList" class="pull_fresh">
      <div class="main">
        <div v-for="(item, index) in dataList" :key="index" class="item" @click="download(item)">
          <div class="fL">
            <i class="iconfont iconword"></i>
            <span class="title time">{{item.Value['MaterialName']}}</span>
          </div>
          <div class="size time fR">
            <span
              v-if="item.Value['MaterialFileSize']>1024*1024"
            >{{ Math.round( item.Value['MaterialFileSize']/ 1024.0/1024 )}} MB</span>
            <span v-else>{{ Math.round( item.Value['MaterialFileSize']/ 1024) }} KB</span>
          </div>
        </div>

        <van-loading v-if="loading" type="spinner" color="#1989fa">加载中...</van-loading>
        <div
          v-else-if=" !dataList || dataList.length === 0"
          style="text-align: center; padding: 1rem;"
        >没有数据~</div>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import { Loading } from "vant";

import headTop from "@/components/headTop";
export default {
  name: "",
  components: { headTop, [Loading.name]: Loading },
  props: {},
  data() {
    return {
      dataList: [],
      loading: false
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.dataList = [];
      this.loading = true;

      let reqData = { courseID: this.$store.getters.courseID };
      this.$mainHttp.courseware
        .getList(reqData)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    download(item) {
      let url = this.$mainHttp.resourse.file(item.Value["MaterialID"]);
      if (url) {
        window.location.href = url;
        this.$toast("下载开始~");
      } else {
        this.$toast("下载失败~");
      }
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
#courseware {
  width: 100%;
  height: 100%;
  .main {
    width: 100%;
    background: #fff;
    max-height: calc(100vh - 4.5rem);
    overflow-y: auto;
    .item {
      overflow: hidden;
      width: 92vw;
      height: 6vh;
      line-height: 6vh;
      margin: 0 auto;
      border-bottom: 1px solid #dddddd;
      span,
      i {
        display: inline-block;
      }
      .iconfont {
        font-size: 1.7rem;
        color: blue;
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem);
    }
  }
}
</style>


